.navbar__container {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  font-family: $openSans;
  height: 8vh;
  position: fixed;
  top: 0;
  background: $theme;
  z-index: 2;
  transition: 0.3s;
  &-rs,
  &-rs-open {
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    a {
      text-decoration: none;
      color: $Gray;
      font-size: 1.2rem;
      font-weight: 500;
      width: max-content;
      text-transform: uppercase;
      margin: 0 10px;
    }
    .current-page {
      color: $White;
      font-weight: 600;
      border-bottom: 1px solid $White;
    }
  }

  // Hamburger Menu
  .menu-btn {
    display: none;
  }
}

// HAMBURGER MENU
.menu-btn__burger {
  width: 30px;
  height: 3px;
  background: $White;
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}
.menu-btn__burger::before,
.menu-btn__burger::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 3px;
  background: $White;
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
  z-index: 999;
}
.menu-btn__burger::before {
  transform: translateY(-10px);
}
.menu-btn__burger::after {
  transform: translateY(10px);
}

// ANIMATION
.menu-btn.open .menu-btn__burger {
  transform: translateX(-50px);
  background: transparent;
}
.menu-btn.open .menu-btn__burger::before {
  transform: rotate(45deg) translate(35px, -35px);
  background: $White;
}
.menu-btn.open .menu-btn__burger::after {
  transform: rotate(-45deg) translate(35px, 35px);
  background: $White;
}

@media only screen and (max-width: 768px) {
  .navbar__container {
    height: 7vh;
    &-rs {
      a {
        display: none;
      }
    }
    &-rs-open {
      justify-content: center;
      width: 100vw;
      position: absolute;
      top: 0;
      height: max-content;
      flex-direction: column;
      background: $theme;
      padding: 10px 0;
      a {
        display: block;
        padding: 15px 0 10px 0;
      }
    }
    // Hamburger Menu
    .menu-btn {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      cursor: pointer;
      z-index: 3;
      transition: all 0.5s ease-in-out;
      margin: 0 20px;
    }
  }
}
