.skills {
  width: 100%;
  height: 100vh;

  background-color: $theme;
  color: $White;
  font-family: $openSans;
  scroll-margin-top: 50px;
  &-container {
    width: 80%;
    height: 100%;
    margin: auto;
    min-height: 100vh;
    padding-top: 40px;
    position: relative;
  }
  &-header {
    font-size: 3rem;
    font-weight: 500;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    .square {
      width: 10px;
      height: 50px;
      background: $skyBlue;
      margin-right: 15px;
    }
  }

  .title-description {
    font-size: 1.2rem;
    font-weight: 500;
    color: $white2;
  }

  &-content {
    width: 100%;
    height: 87%;
    padding-top: 30px;
    position: relative;
    &-name {
      width: 180px;
      height: 180px;
      background-color: #131416;
      backdrop-filter: blur(2.5px);
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      font-weight: 600;
      font-size: 1.2rem;
      position: absolute;
      left: 47%;
      top: 79%;
      transform: scale(0.5);
      z-index: 1;
      img {
        margin-bottom: 5px;
      }
    }
    &-name:nth-child(odd) {
      box-shadow: inset 0px 2px 17px rgba(0, 156, 222, 0.2),
        inset 0px -14px 39px rgba(231, 49, 120, 0.18);
      border-radius: 50%;
      img {
        width: 80px;
        height: 80px;
      }
    }
    &-name:nth-child(even) {
      box-shadow: inset 0px 2px 25px rgba(0, 156, 222, 0.2),
        inset 0px 3px 3px rgba(231, 49, 120, 0.18);
      border-radius: 50%;
      width: 150px;
      height: 150px;
      img {
        width: 70px;
        height: 70px;
      }
    }
  }

  .react {
    animation: react-animation 1s forwards linear;
  }

  .html {
    // left: -10%;
    // animation: htmlSkillAnimation 5s forwards;
    // transform: translate3d(-8vw, 0px, -10px);
    animation: html-animation 1s forwards linear;
  }
  .css {
    animation: css-animation 1s forwards linear;
  }
  .js {
    // transform: translate(40vw, -3vh);
    animation: js-animation 1s forwards linear;
  }
  .cpp {
    // transform: translate(34vw, 35vh);
    animation: cpp-animation 1s forwards linear;
  }
  .c-skill {
    animation: c-animation 1s forwards linear;
  }
  .java {
    // transform: translate(18vw, 11vh);
    animation: java-animation 1s forwards linear;
  }
  .vscode {
    // transform: translate(22vw, 45vh);
    animation: vscode-animation 1s forwards linear;
  }

  .git {
    animation: git-animation 1s forwards linear;
  }

  .bubble {
    position: absolute;
    opacity: 0.8;
    z-index: 0;
  }

  .bubble1 {
    position: absolute;
    width: 50px;
    height: 50px;
    opacity: 0.8;
    top: 10%;
    left: 20%;
  }

  .bubble2 {
    position: absolute;
    width: 70px;
    height: 70px;
    opacity: 0.8;
    top: 20%;
    left: 45%;
  }
  .bubble3 {
    position: absolute;
    width: 60px;
    height: 60px;
    opacity: 0.9;
    top: 80%;
    left: 50%;
  }
  .bubble4 {
    position: absolute;
    width: 150px;
    height: 150px;
    opacity: 0.2;
    bottom: 5%;
    right: 5%;
  }
  .bubble5 {
    position: absolute;
    width: 100px;
    height: 100px;
    opacity: 0.4;
    bottom: 5%;
    left: 5%;
  }
  .bubble6 {
    position: absolute;
    width: 200px;
    height: 200px;
    opacity: 0.2;
    z-index: 0;
    top: 6%;
    left: -15%;
  }
}

@keyframes vscode-animation {
  to {
    top: 52%;
    left: 66%;
    transform: scale(0.7);
  }
}

@keyframes css-animation {
  to {
    left: 20%;
    top: 53%;
    transform: scale(1);
  }
}

@keyframes html-animation {
  to {
    left: 62%;
    top: -8%;
    transform: scale(1);
  }
}
@keyframes js-animation {
  to {
    left: 25%;
    top: 2%;
    // transform: scale(1);
  }
}

@keyframes js-animation-in {
  to {
    transform: scale(1);
    left: 22%;
    top: -7%;
  }
}

@keyframes js-animation-in2 {
  to {
    transform: scale(1);
    left: 8%;
    top: -1%;
  }
}

@keyframes cpp-animation {
  to {
    top: 23%;
    left: 5%;
    transform: scale(0.9);
  }
}
@keyframes c-animation {
  to {
    top: 50%;
    left: -5%;
    transform: scale(0.8);
  }
}

@keyframes c-animation-in {
  to {
    top: 40%;
    left: -9%;
    transform: scale(0.8);
  }
}

@keyframes react-animation {
  to {
    top: 32%;
    left: 44%;
    transform: scale(1.3);
  }
}

@keyframes react-animation-in {
  to {
    top: 25%;
    left: 44%;
    transform: scale(1.1);
  }
}
@keyframes java-animation {
  to {
    top: 38%;
    left: 88%;
    transform: scale(0.8);
  }
}
@keyframes java-animation-it {
  to {
    top: 34%;
    left: 75%;
    transform: scale(0.8);
  }
}

@keyframes git-animation {
  to {
    top: 3%;
    left: 90%;

    transform: scale(0.7);
  }
}
@keyframes git-animation-it {
  to {
    transform: scale(0.7);
    top: 7vh;
    left: 68vw;
  }
}
