/*                                   contact                                   */
/* -------------------------------------------------------------------------- */
.contact {
  position: relative;
  width: 100vw;
  height: 110vh;
  font-family: $openSans;
  background-color: $skyBlue;
  background-color: $darkBlue;
  color: $White;
  overflow: hidden;
  &-container {
    padding-top: 80px;
    width: 80%;
    margin: auto;

    input[type="text"],
    input[type="email"],
    textarea {
      color: $white2;
      border: none;
      outline: none;
      border-bottom: 1px solid $white2;
      background-color: transparent;
      padding: 10px;
      font-size: 1rem;
      display: block;
      width: 100%;
      margin-bottom: 20px;
      resize: none;
    }
    textarea {
      border: 1px solid $white2;
    }
    form {
      width: 50%;
      text-align: center;
    }
    &-content {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
    }

    .submit {
      background-color: transparent;
      padding: 10px 25px;
      color: $white2;
      border: 1px solid $white2;
      cursor: pointer;
      font-size: 1.2rem;
      font-weight: 500;
      &:hover {
        background-color: $white2;
        color: $darkBlue;
      }
    }
  }

  .gratitude {
    text-align: center;
    font-size: 2.5rem;
    margin-top: 40px;
    color: $white2;
  }
}

@media only screen and (max-width: 768px) {
  .skills {
    // height: 120vh;
    height: 70vh;
    &-container {
      width: 90%;
    }
    &-content {
      height: 70%;
      margin-top: 10%;
      padding-top: 0;
      &-name {
        width: 160px;
        height: 160px;
      }

      &-name:nth-child(odd) {
        img {
          width: 70px;
          height: 70px;
        }
      }
      &-name:nth-child(even) {
        width: 150px;
        height: 150px;
        img {
          width: 50px;
          height: 50px;
        }
      }
    }

    .bubble3 {
      top: 51%;
    }

    .js {
      animation-name: js-animation-in;
    }
    .react {
      animation-name: react-animation-in;
    }
    .c-skill {
      animation-name: c-animation-in;
    }
  }

  .contact {
    &-container {
      width: 90%;
    }
  }
}

@media only screen and (max-width: 486px) {
  .skills {
    overflow: hidden;
    &-content {
      width: 100%;
      &-name {
        width: 100px;
        height: 100px;
        font-size: 1rem;
        font-weight: 500;
        img {
          height: 30px !important;
          width: 30px !important;
        }
        &:nth-child(odd) {
        }
        &:nth-child(even) {
          width: 90px;
          height: 90px;
        }
      }
    }

    .java {
      animation-name: java-animation-it;
    }
    .git {
      animation-name: git-animation-it;
    }
    .js {
      animation-name: js-animation-in2;
    }
    .bubble2 {
      width: 62px;
      height: 53px;
      opacity: 0.8;
    }
  }

  .contact {
    form {
      width: 90%;
    }
    .gratitude {
      font-size: 1.2rem;
    }
    path {
      //   d: path("M 0 0 L 100 150 L 100 0 Z");
    }
  }
}
