.projects {
  width: 100%;
  //   height: 100vh;
  background-color: $theme;
  color: $white2;
  padding-top: 100px;
  font-family: $openSans;
  min-height: 100vh;
  &__container {
    width: 80%;
    margin: auto;
    display: flex;
    flex-direction: column;
  }
  &__header {
    font-size: 3rem;
    font-weight: 500;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    .square {
      width: 10px;
      height: 50px;
      background: $skyBlue;
      margin-right: 15px;
    }
  }
  .title-description {
    font-size: 1.5rem;
    font-weight: 500;
  }
  &__list {
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    &__card {
      width: 500px;
      height: 260px;
      margin: 30px;
      transition: 0.5s;
      transform: scale(0.9);
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      //   border-radius: 5px;
      &__image {
        width: 100%;
        height: 100%;
      }
      &__overlay {
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
        height: 30%;
        background: $skyBlue;
        background: $darkBlue;
        transform: translateY(-50%);
        opacity: 0;
        transition: all linear 0.4s;
        color: $White;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        // border-radius: 5px;

        .name {
          font-size: 2rem;
          font-weight: 600;
          margin-bottom: 10px;
        }
        .description {
          font-size: 1rem;
          text-align: center;
        }
        .visit-link {
          //   position: absolute;
          //   bottom: 0;
          //   right: 0;
          //   border-radius: 10px 0 0 0;
          //   background-color: transparent;
          padding: 8px 10px;
          display: flex;
          align-items: center;
          justify-content: space-evenly;
          cursor: pointer;
          border: 1px solid $skyBlue;
          color: $White;
          margin-top: 20px;
          border-radius: 25px;
          a {
            color: inherit;
            text-decoration: none;
          }
          img {
            width: 15px;
            height: 15px;
            margin-left: 10px;
          }

          &:hover {
            background-color: $skyBlue;
          }
        }
        .eye {
          //   position: absolute;
          //   bottom: 0;
          //   right: 0;
          //   border-radius: 10px 0 0 0;
          //   background-color: transparent;
          padding: 5px 20px;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          border: 1px solid $white2;
          color: $white2;
          margin-top: 20px;
          border-radius: 25px;
          a {
            color: inherit;
            text-decoration: none;
          }
          img {
            width: 20px;
            height: 20px;
            margin-right: 5px;
          }
        }
      }

      &:hover {
        transform: scale(1.1);
      }

      &:hover &__overlay {
        // display: block;
        // animation: projectCardOverlay 1s linear;
        height: 100%;
        opacity: 1;
      }
    }
  }
}

@keyframes projectCardOverlay {
  0% {
    height: 0px;
  }

  100% {
    height: 260px;
    transform: translateY(-260px);
  }
}

/* -------------------------------------------------------------------------- */
/*                             PROJECT INFORMATION                            */
/* -------------------------------------------------------------------------- */
.project-info {
  position: absolute;
  width: 100vw;
  height: 100vh;
  overflow: auto;
  background: $theme;
  color: $White;
  font-family: $openSans;
  padding-top: 30px;
  &-container {
    width: 90%;
    margin: auto;
  }

  &-title {
    font-size: 2rem;
    border-bottom: 5px solid $skyBlue;
    margin-bottom: 20px;
    width: max-content;
    padding-bottom: 5px;
  }

  &-images {
    width: 100%;

    &-swiper {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      .swiper-wrapper {
        width: 70%;
      }
      .swiper-slide-shadow {
        background: none;
      }
    }

    &-slide {
      width: 70%;

      img {
        width: 100%;
        height: 52%;
      }
    }
  }

  &-text {
    width: 100%;
    margin-top: 20px;
    text-align: center;
    display: flex;
    text-align: center;
    justify-content: center;
    &-data {
      width: 60%;
      border: 1px solid $Gray;
      font-size: 1.3rem;
      font-weight: 400;
      padding: 20px;
    }
  }
}

@media only screen and (max-width: 768px) {
  .projects {
    &__list {
      &__card {
        &__overlay {
          .name {
            font-size: 1.5rem;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 486px) {
  .projects {
    padding-top: 50px;

    &__container {
      width: 90%;
    }

    .title-description {
      font-size: 1.2rem;
    }

    &__list {
      margin-top: 10px;
      justify-content: center;
      &__card {
        width: 100%;
        height: 220px;
        margin: 20px 0 10px;
        transform: scale(1);
        &:hover {
          transform: scale(1);
        }
      }
    }
  }
}
