:root {
  --gray: #8c8c8c;
  --white: #ffffff;
  --skyBlue: #02a4c4;
  --theme: #131416;
  --white2: #bbbbbb;
  --openSans: "Open Sans", sans-serif;
  --darkBlue: #0b2838;
}

$Gray: var(--gray);
$White: var(--white);
$skyBlue: var(--skyBlue);
$theme: var(--theme);
$white2: var(--white2);
$darkBlue: var(--darkBlue);

//FONTS
$openSans: var(--openSans);
