@import "./variables";
@import "./navbar";
@import "./introduction";
@import "./about";
@import "./projects";
@import "./skills";
@import "./common";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.app__container {
  width: 100vw;
  height: 100vh;
  overflow-y: auto;
  //   overflow-x: hidden;
  scroll-behavior: smooth;
}

.footer {
  position: absolute;
  bottom: 0;
  font-size: 1.2rem;
  font-family: $openSans;
  color: $white2;
  width: 100%;
  padding: 10px;
  background: rgba(18, 98, 153, 0.15);
  display: flex;
  align-items: center;
  justify-content: flex-end;
  &-container {
    display: flex;
    align-items: center;
    .icon-wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      border: 1px solid $white2;
      margin: 0 20px;
      background: $white2;
      height: 40px;
      width: 40px;
    }
    img {
      height: 20px;
      cursor: pointer;
    }
  }
}

.section-title {
  font-size: 3rem;
  font-weight: 500;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  color: $White;
  .square {
    width: 10px;
    height: 50px;
    background: $skyBlue;
    margin-right: 15px;
  }
}

@media only screen and (max-width: 768px) {
  .section-title {
    font-size: 2.5rem;
  }
}

@media only screen and (max-width: 486px) {
  .section-title {
    font-size: 1.5rem;
    .square {
      height: 25px;
      width: 5px;
      margin-right: 10px;
    }
  }

  .footer {
    .icon-wrapper {
      margin: 0 10px;
    }
  }
}
