/* -------------------------------------------------------------------------- */
/*                                INTRODUCTION                                */
/* -------------------------------------------------------------------------- */
.intro {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;

  background-image: url("../../public/assets/introbk1.svg");
  background-size: cover;
  //   background-position: center;
  &-container {
    width: 50%;
    display: flex;
    flex-direction: column;
    font-family: $openSans;
    color: $White;
    margin-left: 10%;
  }
  .homePage-animation {
    display: none;
  }

  &-greet {
    font-size: 3.5rem;
    font-weight: 600;
    margin-bottom: 20px;
  }

  &-name {
    font-size: 3rem;
    margin-bottom: 20px;
    span {
      color: $skyBlue;
    }
  }

  &-role {
    margin-bottom: 20px;
  }

  &-contact {
    width: 150px;
    padding: 10px 0px;
    background-color: transparent;
    // border: 1px solid $Gray;
    border: 1px solid $skyBlue;
    color: $White;
    margin-bottom: 20px;
    font-size: 1.2rem;
    text-align: center;
    border-radius: 25px;
    // background: $White;
    color: $White;
    font-weight: 500;
    cursor: pointer;
    a {
      text-decoration: none;
      color: inherit;
    }
    &:hover {
      background: $skyBlue;
      //   color: $white2;
      cursor: pointer;
    }
    /*  &:hover a {
        co
    } */
  }
}

@media only screen and (max-width: 768px) {
  .intro {
    height: 60vh;
    justify-content: center;
    &-container {
      width: 80%;
      margin-left: 0;
      align-items: center;
    }
  }
}

@media only screen and (max-width: 486px) {
  .intro {
    background: transparent;
    height: 95vh;
    position: relative;
    .homePage-animation {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 0;
      canvas {
        width: 100vw !important;
        position: static !important;
        height: 100vh !important;
      }
    }
    &-container {
      width: 90%;
      z-index: 1;
    }
    &-name {
      font-size: 2rem;
    }
  }
}
