.abm-container {
  width: 100%;
  height: 84vh;
  padding-top: 8vh;
  background: $theme;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: $openSans;
  color: #bbbbbb;
  z-index: 2;
  &-textual {
    width: 40%;
    margin-right: 30px;
  }

  &-title {
    font-size: 3rem;
    font-weight: 500;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    .square {
      width: 10px;
      height: 50px;
      background: $skyBlue;
      margin-right: 10px;
    }
  }

  &-peach-line {
    font-size: 1.5rem;
    letter-spacing: 3px;
    font-weight: 400;
    margin-bottom: 20px;
  }

  &-intro {
    font-size: 1rem;
    font-family: 500;
    line-height: 2rem;
    letter-spacing: 1px;
  }

  &-btns {
    margin-top: 30px;
    .resume {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 10px;
      width: 200px;
      border: 1px solid #bbbbbb;
      cursor: pointer;
      border-radius: 25px;
      &__image {
        margin-right: 10px;
      }

      &__card {
        width: 500px;
        height: 260px;
      }
      img {
        margin-right: 10px;
      }
    }
  }

  &-photo {
    img {
      width: 500px;
      height: 500px;
      border-radius: 50%;
    }
  }
}

.analytics {
  width: 100%;
  height: 16vh;
  background: $theme;
  font-family: $openSans;
  &__container {
    width: 80%;
    height: 100%;
    border-radius: 50px 50px 0px 0px;
    background-color: #1f1f1f;
    margin: auto;
    color: $white2;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    &__card {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      font-size: 1.5rem;

      text-align: center;
      .count {
        margin-bottom: 10px;
        color: $skyBlue;
      }
      .name {
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  .abm-container {
    height: max-content;
    flex-direction: column;
    justify-content: flex-start;
    &-textual {
      width: 90%;
      margin-right: 0;
      margin: 40px 0;
      p {
        margin-top: 10px;
      }
    }

    &-photo {
      order: -1;
      img {
        width: 50vw;
        height: 50vw;
      }
    }

    &-btns {
      width: 100%;
      display: flex;
      justify-content: center;
    }
  }

  .analytics {
    height: 14vh;
    &__container {
      width: 100%;
      border-radius: 0;
    }
  }
}
@media only screen and (max-width: 486px) {
  .abm-container {
    &-peach-line {
      font-size: 1.2rem;
    }
  }
  .analytics__container {
    &__card {
      font-size: 0.9rem;
    }
  }
}
